"use client"

import { useState, useEffect } from 'react'
import { VideoCreationForm } from '@/components/video-creation-form'
import { VideoPreview } from '@/components/video-preview'
import { LoadingIndicator } from '@/components/loading-indicator'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { useUserStore, createMockUser, getVideoQuota } from '@/lib/store'
import { toast } from 'sonner'

export default function CreateVideoPage() {
  const [isGenerating, setIsGenerating] = useState(false)
  const [generatedVideo, setGeneratedVideo] = useState<string | null>(null)
  const [error, setError] = useState<string | null>(null)
  
  const { user, setUser, canCreateVideo, getRemainingVideos, incrementVideoCount } = useUserStore()
  
  // Initialize mock user if not authenticated
  useEffect(() => {
    if (!user) {
      setUser(createMockUser('free'))
    }
  }, [user, setUser])
  
  const userTier = user?.tier || 'free'
  const userId = user?.id || 'anonymous'
  const quota = getVideoQuota(userTier)
  const remainingVideos = getRemainingVideos()

  const handleVideoGeneration = async (data: any) => {
    // Check if user can create video
    if (!canCreateVideo()) {
      toast.error('Günlük video limitiniz doldu. Premium\'a geçin veya yarın tekrar deneyin.')
      return
    }

    setIsGenerating(true)
    setError(null)
    setGeneratedVideo(null)

    try {
      // Add user information to the request
      const requestData = {
        ...data,
        userTier,
        userId,
      }

      const response = await fetch('/api/generate-video', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(requestData),
      })

      if (!response.ok) {
        const errorData = await response.json()
        throw new Error(errorData.error || 'Video oluşturma başarısız')
      }

      const result = await response.json()
      setGeneratedVideo(result.videoUrl)
      
      // Increment video count after successful generation
      incrementVideoCount()
      toast.success('Video başarıyla oluşturuldu!')
      
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Bir hata oluştu')
      toast.error(err instanceof Error ? err.message : 'Video oluşturma başarısız')
    } finally {
      setIsGenerating(false)
    }
  }

  return (
    <div className="max-w-4xl mx-auto space-y-8">
      <div className="text-center space-y-4">
        <h1 className="text-3xl font-bold">AI Video Oluşturucu</h1>
        <p className="text-muted-foreground">
          Metin veya sesli komutlarla profesyonel videolar oluşturun
        </p>
        {userTier === 'free' && (
          <div className="bg-gradient-to-r from-blue-50 to-purple-50 border border-blue-200 rounded-lg p-4">
            <div className="flex items-center justify-center space-x-2 text-sm">
              <span className="text-blue-600 font-medium">Ücretsiz Plan</span>
              <span className="text-muted-foreground">•</span>
              <span className="text-muted-foreground">Maksimum 30 saniye</span>
              <span className="text-muted-foreground">•</span>
              <span className="text-muted-foreground">Filigran ile</span>
              <span className="text-muted-foreground">•</span>
              <span className={`font-medium ${remainingVideos > 0 ? 'text-green-600' : 'text-red-600'}`}>
                {remainingVideos} video kaldı
              </span>
            </div>
            <div className="mt-2 flex items-center justify-center space-x-4">
              <a 
                href="/premium" 
                className="text-blue-600 hover:text-blue-700 text-sm font-medium underline"
              >
                Premium'a geçin ve sınırları kaldırın →
              </a>
              {remainingVideos === 0 && (
                <span className="text-red-600 text-sm font-medium">
                  Yarın yeni video hakkınız gelecek
                </span>
              )}
            </div>
          </div>
        )}
      </div>

      <div className="grid lg:grid-cols-2 gap-8">
        {/* Video Creation Form */}
        <Card>
          <CardHeader>
            <CardTitle>Video Ayarları</CardTitle>
            <CardDescription>
              Videonuzun içeriğini ve stilini belirleyin
            </CardDescription>
          </CardHeader>
          <CardContent>
            <VideoCreationForm 
              onSubmit={handleVideoGeneration}
              isLoading={isGenerating}
              userTier={userTier}
            />
          </CardContent>
        </Card>

        {/* Video Preview */}
        <Card>
          <CardHeader>
            <CardTitle>Önizleme</CardTitle>
            <CardDescription>
              Oluşturulan videonuz burada görünecek
            </CardDescription>
          </CardHeader>
          <CardContent>
            {isGenerating && <LoadingIndicator />}
            {error && (
              <div className="p-4 bg-destructive/10 text-destructive rounded-lg">
                {error}
              </div>
            )}
            {generatedVideo && (
              <VideoPreview videoUrl={generatedVideo} />
            )}
            {!isGenerating && !generatedVideo && !error && (
              <div className="flex items-center justify-center h-64 bg-muted rounded-lg">
                <p className="text-muted-foreground">
                  Video oluşturmak için formu doldurun
                </p>
              </div>
            )}
          </CardContent>
        </Card>
      </div>

      {/* Tips Section */}
      <Card>
        <CardHeader>
          <CardTitle>İpuçları</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="grid md:grid-cols-2 gap-4 text-sm">
            <div>
              <h4 className="font-medium mb-2">Etkili Prompt Yazma:</h4>
              <ul className="space-y-1 text-muted-foreground">
                <li>• Detaylı açıklamalar kullanın</li>
                <li>• Görsel öğeleri belirtin</li>
                <li>• Duygu ve atmosferi tanımlayın</li>
                <li>• Karakter özelliklerini ekleyin</li>
              </ul>
            </div>
            <div>
              <h4 className="font-medium mb-2">Sesli Komut İpuçları:</h4>
              <ul className="space-y-1 text-muted-foreground">
                <li>• Açık ve net konuşun</li>
                <li>• Sessiz bir ortam seçin</li>
                <li>• Yavaş ve anlaşılır telaffuz</li>
                <li>• Noktalama işaretlerini söyleyin</li>
              </ul>
            </div>
          </div>
        </CardContent>
      </Card>
    </div>
  )
}
